.footer__copyright {
    font-family: 'Inter','Arial',sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;

    color: #545454;
}

@media screen and (min-width:320px) and (max-width:1279px) {
    .footer__copyright {
        margin-left: 0 auto;
        font-family: 'Inter','Arial',sans-serif;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;

        color: #545454;

    }
}